const { createClient } = require('@supabase/supabase-js');
require('dotenv').config();

const supabase = createClient(
  process.env.SUPABASE_URL,
  process.env.SUPABASE_SERVICE_ROLE_KEY
);

async function createTables() {
  console.log('üöÄ Criando tabelas b√°sicas...');
  
  try {
    // Primeiro, vamos tentar criar uma tabela simples para testar
    console.log('üìä Testando conex√£o...');
    
    // Tentar acessar uma tabela existente ou criar uma de teste
    const { data: testData, error: testError } = await supabase
      .from('test_table')
      .select('*')
      .limit(1);
    
    if (testError && testError.message.includes('does not exist')) {
      console.log('‚úÖ Conex√£o OK - tabelas n√£o existem ainda');
    } else if (testError) {
      console.log('‚ö†Ô∏è  Erro de conex√£o:', testError.message);
      return;
    }
    
    // Vamos usar uma abordagem diferente - criar dados diretamente
    console.log('üí° Tentando abordagem alternativa...');
    
    // Verificar se j√° existem dados nas tabelas
    const tables = ['companies', 'branches', 'users', 'clients', 'contracts', 'payments'];
    
    for (const tableName of tables) {
      try {
        const { data, error } = await supabase
          .from(tableName)
          .select('id')
          .limit(1);
        
        if (error) {
          console.log(`‚ùå Tabela ${tableName} n√£o existe: ${error.message}`);
        } else {
          console.log(`‚úÖ Tabela ${tableName} j√° existe`);
        }
      } catch (err) {
        console.log(`‚ùå Erro ao verificar ${tableName}: ${err.message}`);
      }
    }
    
    console.log('\nüìã INSTRU√á√ïES MANUAIS:');
    console.log('Como as tabelas n√£o foram criadas automaticamente, voc√™ precisa:');
    console.log('1. Abrir o Supabase Dashboard');
    console.log('2. Ir para SQL Editor');
    console.log('3. Executar o conte√∫do do arquivo: src/migrations/init.sql');
    console.log('4. Depois executar novamente este script para testar');
    
  } catch (error) {
    console.error('‚ùå Erro geral:', error.message);
  }
}

createTables();